@font-face {
    font-family: 'CustomFont1';
    src: url('../../fonts/GT-America-Expanded-Bold-Trial.otf') format('truetype');
    /* You can specify additional font properties if needed */
  }

  @font-face {
    font-family: 'CustomFont2';
    src: url('../../fonts/GT-America-Expanded-Regular-Trial.otf') format('truetype');
    /* You can specify additional font properties if needed */
  }



.GoDigital{
    padding: 0 5vw 5vh 5vw;
}

.godigital-main{
    background-color: white;
    display: flex;
    border-radius: 40px;
    padding: 0 5vw 0 5vw;
    height: 60vh;
}

.godigital-main-leftpane{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 20vw;
}

.godigital-main-rightpane{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    padding-left: 5vw;
}

.godigital-main-rightpane-header{
    font-family: 'CustomFont1';
    font-size: 3vw;
}

.godigital-main-rightpane-row{
    font-family: 'CustomFont2';
    width: 40vw;
    font-size: 1vw;
}

.godigital-text-get-started{
    font-size: 1.2vw;
    font-family: 'CustomFont1';
    color: white;
}

.godigital-btn{
    background: #ED7E02;
border-radius: 100px;
width: 20vw;
display: flex;
height: 10vh;
justify-content: space-evenly;
align-items: center;
}